import React from 'react';
import "./Profile.module.css"
import {Profile} from "./Profile";
import {connect} from "react-redux";
import {RootReducerType} from "../../redux/redux-store";
import axios from "axios";
import {ResponseProfileType, setUserProfile} from "../../redux/profileReducer";

type MapStateToPropsType = {
    profile:ResponseProfileType
}

type MapDispatchToPropsType = {
    setUserProfile: (data:ResponseProfileType) => void
}
type ProfilePageType = MapStateToPropsType & MapDispatchToPropsType

class ProfileAPIComponent extends React.Component<ProfilePageType> {

    componentDidMount() {
        axios.get<ResponseProfileType>(`https://social-network.samuraijs.com/api/1.0/profile/2`)
            .then((response) => {
                this.props.setUserProfile(response.data)
            });
    }

    render() {
        return (
            <Profile {...this.props} profile={this.props.profile}/>
        )
    }

}

const mapStateToProps = (state: RootReducerType):MapStateToPropsType  => ({
    profile:state.profileReducer.profile
})

export const ProfileContainer = connect(mapStateToProps, {setUserProfile})(ProfileAPIComponent)